.friendList {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  list-style: none;
  margin-bottom: 0;
}

.friendItem {
  display: flex;
  width: 200px;
  padding: 25px;
  gap: 20px;
  margin-top: 0;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.onLine {
  width: 15px;
  height: 20px;
  background-color: darkgreen;
  border-radius: 10px;
  padding-top: 15%;
}

.offLine {
  width: 15px;
  height: 20px;
  background-color: red;
  border-radius: 10px;
  padding-top: 15%;
}

.name {
  font-weight: 400;
  font-size: 25px;
  line-height: calc(50 / 25);
}
